import React from "react";
// 导入组件
import Code from "./statistical-learn-components/code"
import Note from "./statistical-learn-components/note"
import Title from "./statistical-learn-components/title"
import Console from "./statistical-learn-components/console"
import AlgorithmSelect from "./statistical-learn-components/algorithmSelect";
import ModelSelect from "./statistical-learn-components/modelSelect";
// 导入常量
import {Themes} from "../assest/Theme"
// 导入相关css样式
import "./statistical-learn-components/css/sliding.css"

interface StatisticalProps{
}

interface StatisticalState{
}

function Statistical(props: StatisticalProps, state: StatisticalState){
    let WidthPage = window.innerWidth > 900 ? window.innerWidth : 900
    let HeightPage = window.innerHeight > 450 ? window.innerHeight - 1 : 450
    const {
        TitleTheme,
        CodeTheme,
        ConsoleTheme,
        NoteTheme,
        AlgorithmSelectTheme,
        ModelSelectTheme
    } = Themes


    return (
        <div className="Statistical" style={{overflow: 'hidden'}}>
            <Title WidthPage={WidthPage} HeightPage={HeightPage} Theme_begin={TitleTheme}></Title>
            <Code WidthPage={WidthPage} HeightPage={HeightPage} Theme_begin={CodeTheme}></Code>
            <Console WidthPage={WidthPage} HeightPage={HeightPage} Theme_begin={ConsoleTheme}></Console>
            <Note WidthPage={WidthPage} HeightPage={HeightPage} Theme_begin={NoteTheme}></Note>
            {/* 悬浮框 */}
            <AlgorithmSelect Theme_begin={AlgorithmSelectTheme} WidthPage={WidthPage} HeightPage={HeightPage}></AlgorithmSelect>
            <ModelSelect Theme_begin={ModelSelectTheme} WidthPage={WidthPage} HeightPage={HeightPage}></ModelSelect>
        </div>
    )
}

export default Statistical