import {createSlice} from "@reduxjs/toolkit"
// 导入常量
import {
    Algorithm,
    Algorithm_Code
} from "../../assest/algorithm"

const Reducer_module = createSlice({
    name: "reducer_module",
    // 初始化状态数据state
    initialState: { 
        Code_StrData: (Algorithm_Code as any)[Algorithm[0].name][Algorithm[0].model[0]], // 代码编辑器传出的数据
        AlgorithmNow: Algorithm[0].name, // 当前展示的模型的名称
        ModelNow: Algorithm[0].model[0], // 当前展示的模型算法的名称
    },
    // 主要的reducers
    reducers: {
        // 代码编辑器传入的要运行的js字符串数据
        Change_Code_StrData(state, action){
            state.Code_StrData = action.payload
        },
        // 改变当前展示的模型的名称
        Change_AlgorithmNow(state, action){
            state.AlgorithmNow = action.payload
            for(let i = 0; i < Algorithm.length; i++){
                if(Algorithm[i].name === state.AlgorithmNow){
                    // 更新算法的模型和代码
                    state.ModelNow = Algorithm[i].model[0]
                    state.Code_StrData = (Algorithm_Code as any)[state.AlgorithmNow][state.ModelNow]
                    break
                }
            }
        },
        // 改变当前展示的模型算法的名称
        Change_ModelNow(state, action){
            state.ModelNow = action.payload
            // 更新算法模型的代码
            state.Code_StrData = (Algorithm_Code as any)[state.AlgorithmNow][state.ModelNow]
        }
    } 
})

// 导出action函数
export const {
    Change_Code_StrData,
    Change_AlgorithmNow,
    Change_ModelNow
} = Reducer_module.actions

// 导出reducer，创建store用
export default Reducer_module.reducer