import React from "react";

interface TitleProps{
    Theme_begin: any,
    WidthPage: number,
    HeightPage: number
}

interface TitleState{
}

function Title(props: TitleProps, state: TitleState){
    const {Theme_begin, WidthPage, HeightPage} = props
    let Theme = {
        ...Theme_begin,
        width: Theme_begin.width * WidthPage
    }

    return (
        <div className="Title" style={{...Theme}}></div>
    )
}

export default Title  