import React from "react";
// 导入redux
import {useSelector} from "react-redux"

interface ConsoleProps{
    Theme_begin: any,
    WidthPage: number,
    HeightPage: number
}

interface ConsoleState{
    Show_data: string, // js字符串的运行结果
}

function Console(props: ConsoleProps, state: ConsoleState){
    // 常量
    const {Theme_begin, WidthPage, HeightPage} = props
    const ConsoleHeight = 25
    const LeftWidth = 30
    // 导入redux
    const {Code_StrData} = useSelector(state => (state as any).Reducer_module)
    // state
    const [Show_data, Set_Show_data] = React.useState("")

    React.useEffect(() => {
        let show_data = ""
        // 重写console.log()
        let OldLog = console.log
        console.log = function(value: any){
            OldLog(value)
            if(value){
                show_data += (value.toString() + "<br />")
            }
            // show_data += (value.toString() + "<br />")
        }
        // 初始化show_data每次
        show_data = ""
        // 执行js格式字符串代码(含错误处理)
        try{
            window.eval(Code_StrData)
        } catch(error){
            show_data = (error as any).toString()
        } finally{
            // 更新前端展示
            Set_Show_data(show_data)
        }
        
        
    }, [Code_StrData])

    let Theme = {
        ...Theme_begin,
        width: Theme_begin.width * WidthPage,
        height: Theme_begin.height * HeightPage,
        top: Theme_begin.top * HeightPage
    }

    return (
        <div className="Console" style={{...Theme}}>
            <div style={{float: 'left', color: 'rgb(240, 240, 240)', background: "rgb(40, 40, 40)", width: Theme.width, height: ConsoleHeight, lineHeight: ConsoleHeight.toString() + 'px', fontSize: '15px', fontWeight: 600}}>
                &nbsp;🦀Console
            </div>
            <div style={{float: 'left', color: 'rgb(240, 240, 240)', textAlign: 'center', width: LeftWidth, height: Theme.height - ConsoleHeight}}>
                {">"}
            </div>
            <div style={{float: 'left', color: 'rgb(240, 240, 240)', width: Theme.width - LeftWidth - 2, height: Theme.height - ConsoleHeight, borderLeft: '2px dashed rgb(174, 129, 255)'}}>
                {/* dangerouslySetInnerHTML={{_html: }} 将数据转成html语句展示出来 */}
                <div style={{float: 'left', width: Theme.width - LeftWidth - 2 - 10, height: Theme.height - ConsoleHeight, marginLeft: 10, overflow: 'auto'}} dangerouslySetInnerHTML={{__html: Show_data}}>
                    {/* {Show_data} */}
                </div>
            </div>
        </div>
    )
}

export default Console  