import React from "react";
// 导入常量
import {Algorithm} from "../../assest/algorithm"
// 导入redux
import {useDispatch, useSelector} from "react-redux"
import {
    Change_AlgorithmNow
} from "../../store/reducer/index_reducer"
// 引入样式
import "./css/common.css"

interface AlgorithmSelectProps{
    Theme_begin: any,
    WidthPage: number,
    HeightPage: number
}

interface AlgorithmSelectState{
    Algorithm_Element: any[], // 供选择的算法的名称标签
}

function AlgorithmSelect(props: AlgorithmSelectProps, state: AlgorithmSelectState){
    const {Theme_begin, WidthPage, HeightPage} = props
    const Theme = {
        ...Theme_begin,
        left: (WidthPage - Theme_begin.width) / 2,
        top: (HeightPage - Theme_begin.height) / 2,
    }
    const TopTitleHeight = 40 // 顶部title所占的高度
    const ButtonBackWidth = 40 // 退出按钮所占的宽度
    const Show_item_num = 12 // 一次可以展示的Algorithm名称的数量
    const Show_item_height = (Theme.height - TopTitleHeight) / Show_item_num // 每个Algorithm的高度
    // state
    const [Algorithm_Element, Set_Algorithm_Element] = React.useState([])
    // redux
    const {AlgorithmNow} = useSelector(state => (state as any).Reducer_module)
    const dispatch = useDispatch()

    React.useEffect(() => {
        Get_algorithm_element(AlgorithmNow)
    }, [])

    /**
     * @Get_algorithm_element 获取Algorithm_Element函数(按照当前情况及时更新当前的Algorithm)
     * @param algorithm_before 
     */
    function Get_algorithm_element(algorithm_before: string){
        let algorithm_element = Algorithm.map((v, index) => {
            return <div className="Show-Item" key={v.name + '-' + index.toString()} 
                style={{float: 'left', width: Show_item_num < Algorithm.length ? Theme.width - 7 : Theme.width, height: Show_item_height, borderBottom: ((index !== Algorithm.length) || (Algorithm.length < Show_item_num)) ? "1px solid rgb(180, 180, 180)" : ""}}
                onClick={() => {
                    if(v.name !== algorithm_before){
                        // 关闭窗口
                        let element: any = document.getElementById("Algorithm-Select-border")
                        element.style.display = 'none'
                        // 修改当前的算法
                        dispatch(Change_AlgorithmNow(v.name))
                    } else {
                        alert("Invalid selection!")
                    }
                }}
            >
                {v.name}
            </div>
        })
        Set_Algorithm_Element(algorithm_element as any)
    }

    React.useEffect(() => {
        Get_algorithm_element(AlgorithmNow)
    }, [AlgorithmNow])

    return (
        <div id="Algorithm-Select-border" className="AlgorithmSelect" style={{...Theme, display: 'none'}}>
            <div className="Select-border" style={{float: 'left', width: "100%", height: TopTitleHeight, lineHeight: TopTitleHeight.toString() + 'px', borderBottom: "2px dashed rgb(180, 180, 180)"}}>
                <div style={{float: 'left', width: "100%", height: TopTitleHeight}}>
                    Algorithm Select
                </div>
                <div className="ButtonBack" style={{float: 'left', width: ButtonBackWidth, height: TopTitleHeight, marginLeft: -ButtonBackWidth}}
                    onClick={() => {
                        let element: any = document.getElementById("Algorithm-Select-border")
                        element.style.display = 'none'
                    }}
                >✗</div>
            </div>
            {/* Algorithm选择部分 */}
            <div style={{float: 'left', width: '100%', height: Theme.height - TopTitleHeight, overflow: 'auto', lineHeight: Show_item_height.toString() + 'px', textAlign: 'center', fontSize: "10px", cursor: 'pointer'}}>
                {Algorithm_Element}
            </div>
        </div>
    )
}

export default AlgorithmSelect