import React from "react";
// 导入常量
import {Algorithm} from "../../assest/algorithm"
// 导入redux
import {useDispatch, useSelector} from "react-redux"
import {
    Change_ModelNow
} from "../../store/reducer/index_reducer"
// 引入样式
import "./css/common.css"

interface ModelSelectProps{
    Theme_begin: any
    WidthPage: number,
    HeightPage: number
}

interface ModelSelectState{
    Model_Element: any[], // 供选择的算法模型的名称标签 
}

function ModelSelect(props: ModelSelectProps, state: ModelSelectState){
    const {Theme_begin, WidthPage, HeightPage} = props
    const Theme = {
        ...Theme_begin,
        left: (WidthPage - Theme_begin.width) / 2,
        top: (HeightPage - Theme_begin.height) / 2,
    }
    const TopTitleHeight = 40 // 顶部title所占的高度
    const ButtonBackWidth = 40 // 退出按钮所占的宽度
    const Show_item_num = 12 // 一次可以展示的Algorithm名称的数量
    const Show_item_height = (Theme.height - TopTitleHeight) / Show_item_num // 每个Algorithm的高度
    // state
    const [Model_Element, Set_Model_Element] = React.useState([])
    // redux
    const {AlgorithmNow, ModelNow} = useSelector(state => (state as any).Reducer_module)
    const dispatch = useDispatch()

    React.useEffect(() => {
        Get_model_element(AlgorithmNow, ModelNow)
    }, [])

    /**
     * @Get_model_element 获取Algorithm_Element函数(按照当前情况及时更新当前的Algorithm)
     * @param algorithm_before 当前的算法名称
     * @param model_before 先前的算法模型名称
     */
    function Get_model_element(algorithm_before: string, model_before: string){
        for(let i = 0; i < Algorithm.length; i++){
            if(Algorithm[i].name === algorithm_before){
                let model_element = Algorithm[i].model.map((v, index) => {
                    return <div className="Show-Item" key={algorithm_before + '-' + v + '-' + index.toString()} 
                        style={{float: 'left', width: Show_item_num < Algorithm[i].model.length ? Theme.width - 7 : Theme.width, height: Show_item_height, borderBottom: ((index !== Algorithm[i].model.length) || (Algorithm[i].model.length < Show_item_num)) ? "1px solid rgb(180, 180, 180)" : ""}}
                        onClick={() => {
                            if(v !== model_before){
                                // 关闭窗口
                                let element: any = document.getElementById("Model-Select-border")
                                element.style.display = 'none'
                                // 修改当前的算法
                                dispatch(Change_ModelNow(v))
                            } else {
                                alert("Invalid selection!")
                            }
                        }}
                    >
                        {v}
                    </div>
                })
                Set_Model_Element(model_element as any)
                break
            }
        }
        
    }

    React.useEffect(() => {
        Get_model_element(AlgorithmNow, ModelNow)
    }, [ModelNow])

    return(
        <div id="Model-Select-border" className="ModelSelect" style={{...Theme, display: 'none'}}>
            <div className="Select-border" style={{float: 'left', width: "100%", height: TopTitleHeight, lineHeight: TopTitleHeight.toString() + 'px', borderBottom: "2px dashed rgb(180, 180, 180)"}}>
                <div style={{float: 'left', width: "100%", height: TopTitleHeight}}>
                    Model Select
                </div>
                <div className="ButtonBack" style={{float: 'left', width: ButtonBackWidth, height: TopTitleHeight, marginLeft: -ButtonBackWidth}}
                    onClick={() => {
                        let element: any = document.getElementById("Model-Select-border")
                        element.style.display = 'none'
                    }}
                >✗</div>
            </div>
            {/* Algorithm选择部分 */}
            <div style={{float: 'left', width: '100%', height: Theme.height - TopTitleHeight, overflow: 'auto', lineHeight: Show_item_height.toString() + 'px', textAlign: 'center', fontSize: "10px", cursor: 'pointer'}}>
                {Model_Element}
            </div>
        </div>
    )
}

export default ModelSelect