import React from "react";
// 导入常量
import {Themes} from "../../assest/Theme"
import { Algorithm_Note } from "../../assest/algorithm";
// redux
import {useSelector} from 'react-redux'

import MdEditor from "md-editor-rt"
import "md-editor-rt/lib/style.css"

interface NoteProps{
    Theme_begin: any,
    WidthPage: number,
    HeightPage: number
}

interface NoteState{
    Show_mark: string, // 要展示的markdown转成的HtML格式元素
}

function Note(props: NoteProps, state: NoteState){
    const {Theme_begin, WidthPage, HeightPage} = props
    let Theme = {
        ...Theme_begin,
        width: Theme_begin.width * WidthPage - 4,
        height: Theme_begin.height * HeightPage - Themes.TitleTheme.height,
        left: Theme_begin.left * WidthPage
    }
    // redux
    const {AlgorithmNow} = useSelector(state => (state as any).Reducer_module)
    const [Show_mark, Set_Show_mark] = React.useState("")

    React.useEffect(() => {
        let show_str = (Algorithm_Note as any)[AlgorithmNow]
        let str_list = show_str.split("$$")
        show_str = str_list[0]
        for(let i = 1; i < str_list.length; i+=2){
            let mid_str = str_list[i].replace(/\r\n/g, " ") // 替换掉所有的\r\n
            // mid_str = mid_str.replace(/\u0026/g, "&")
            // mid_str = mid_str.replace(/\u003e/g, ">")
            // mid_str = mid_str.replace(/\u003c/g, "<")
            show_str += ("$" + mid_str + "$ " + str_list[i + 1]) // 
        }
        // console.log(show_str)
        Set_Show_mark(show_str)
    }, [AlgorithmNow])

    return (
        <div className="Note" style={{...Theme, overflow: 'auto'}}>
            <div
                id="Note-Container" 
                style={{float: 'left', width: Theme.width, height: Theme.height, overflow: 'auto'}}
                // dangerouslySetInnerHTML={{__html: "<InlineMath>f(x)=sign(w\cdot x + b)</InlineMath>"}}
            >
                <MdEditor 
                    style={{float: 'left', width: Theme.width - 15, marginLeft: 5}}
                    modelValue={Show_mark}
                    // pageFullScreen={true}
                    previewOnly={true}
                ></MdEditor>
            </div>
        </div>
    )
}

export default Note  