import React from "react";
// 导入常量
import {Themes} from "../../assest/Theme"
import {Algorithm, Algorithm_Code} from "../../assest/algorithm"
// 导入css样式
import "./css/common.css"

// 导入CodeMirror（CodeMirror相关配置）
import CodeMirror from "react-codemirror"
// 导入相关关键文件
import "codemirror/lib/codemirror.js"
import "codemirror/lib/codemirror.css"
// 导入主题
import "codemirror/theme/seti.css"
// 导入代码模式
import "codemirror/mode/javascript/javascript"
//代码高亮
import 'codemirror/addon/selection/active-line';
// 代码折叠
import 'codemirror/addon/fold/foldgutter.css'; 
import 'codemirror/addon/fold/foldcode.js';
import 'codemirror/addon/fold/foldgutter.js';  
import 'codemirror/addon/fold/brace-fold.js';  
import 'codemirror/addon/fold/comment-fold.js';
// 括号匹配
import "codemirror/addon/edit/matchbrackets.js"
import "codemirror/addon/edit/closebrackets.js"
// 自动补全
import "codemirror/addon/hint/show-hint.css"
import "codemirror/addon/hint/show-hint.js"
import "codemirror/addon/hint/anyword-hint.js"

// 导入redux
import {useDispatch, useSelector} from "react-redux"
import {Change_Code_StrData} from "../../store/reducer/index_reducer"


interface CodeProps{
    Theme_begin: any,
    WidthPage: number,
    HeightPage: number
}

interface CodeState{
    Code_StrData_now: string, // 代码编辑器里面的字符串数据
    Code_Mirror_Element: any // CodeMirror的element变量
}

function Code(props: CodeProps, state: CodeState){
    // 常量
    const {Theme_begin, WidthPage, HeightPage} = props
    let Theme = {
        ...Theme_begin,
        width: Theme_begin.width * WidthPage,
        height: Theme_begin.height * HeightPage - Themes.TitleTheme.height
    }
    let ButtonHeight = 25 // run按钮的高度
    let AlgorithmWidth = 80 //
    // State
    const [Code_StrData_now, Set_Code_StrData_now] = React.useState((Algorithm_Code as any)[Algorithm[0].name][Algorithm[0].model[0]])
    const [Code_Mirror_Element, Set_Code_Mirror_Element] = React.useState([])
    // redux的dispatch
    const dispatch = useDispatch()
    const {AlgorithmNow, ModelNow, Code_StrData} = useSelector(state => (state as any).Reducer_module)

    React.useEffect(() => {
        let code_mirror_element = [
            <CodeMirror
                key={AlgorithmNow + '-' + ModelNow}
                className="CodeMirror-System"
                value={Code_StrData}
                options={{
                    mode: 'javascript',
                    theme: "seti",
                    lineNumbers: true,
                    autofocus: true, // 自动获取焦点
                    styleActiveLine: true, // 光标代码高亮
                    // 代码折叠
                    lineWrapping: true, 
                    foldGutter: true, 
                    gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter'],
                    // 括号匹配
                    matchBrackets: true,
                    // 括号补全
                    autoCloseBrackets: true,
                    // 智能提示(即自动补全，但是只能补全已经写过的东东)
                    extraKeys:{"Ctrl": "autocomplete"} // 按Ctrl键唤起智能提示
                }}
                onChange={(code) => {
                    // dispatch(Change_Code_StrData(code))
                    // Set_Code_StrData(code)
                    Set_Code_StrData_now(code)
                }}
            ></CodeMirror>
        ]
        Set_Code_Mirror_Element(code_mirror_element as any)
        Set_Code_StrData_now(Code_StrData)
    }, [ModelNow])

    
    return (
        <div className="Code" style={{...Theme}}>
            {/* 运行按钮 */}
            <div className="run-button" style={{float: 'right', width: 60, height: ButtonHeight, lineHeight: ButtonHeight.toString() + 'px'}}
                onClick={() => {
                    dispatch(Change_Code_StrData(Code_StrData_now))
                }}
            >▶ Run</div>
            {/* 确定当前的模型及模型算法部分 */}
            <div style={{float: 'right', background: 'rgb(40, 40, 40)', width: Theme.width - 60, height: ButtonHeight, lineHeight: ButtonHeight.toString() + 'px', textAlign: 'center'}}>
                <div style={{float: 'left', width: AlgorithmWidth, height: ButtonHeight, color: 'rgb(240, 240, 240)'}}>Algorithm:</div>
                <div style={{float: 'left', width: (Theme.width - 60 - AlgorithmWidth * 2) / 2 - 10, marginLeft: 5, height: ButtonHeight, borderRadius: '5px', cursor: "pointer", background: "rgb(230, 230, 230)"}}
                    onClick={() => {
                        let element: any = document.getElementById("Algorithm-Select-border")
                        element.style.display = 'inline'
                    }}
                >{AlgorithmNow}</div>
                <div style={{float: 'left', width: AlgorithmWidth, height: ButtonHeight, marginLeft: 5, color: 'rgb(240, 240, 240)'}}>Model:</div>
                <div style={{float: 'left', width: (Theme.width - 60 - AlgorithmWidth * 2) / 2 - 10, marginLeft: 5, height: ButtonHeight, borderRadius: '5px', cursor: "pointer", background: "rgb(230, 230, 230)"}}
                    onClick={() => {
                        let element: any = document.getElementById("Model-Select-border")
                        element.style.display = 'inline'
                    }}
                >{ModelNow}</div>
            </div>
            {/* 代码编辑器部分 */}
            <div id="CodeMirror-container" style={{float: 'right', width: Theme.width, height: Theme.height - ButtonHeight}}>
                {Code_Mirror_Element}
            </div>
        </div>
    )
}

export default Code